        color: $main;
        width: 100vw;
        height: 100vh;
        transition: all 0.4s ease-in-out;
        transform: translateY(-100%);

        &::before{
            content: "";
            position: absolute;
            background: url(../assets/blob2.svg);
            height: 35vw;
            width: 35vw;
            top: 2rem;
            left: 2rem;
            opacity: 0.4;
            transform: rotateZ(-45deg);
        }
        &::after{
            content: "";
            position: absolute;
            background: url(../assets/blob1.svg);
            height: 30vw;
            width: 30vw;
            bottom: 2rem;
            right: 2rem;
            opacity: 0.4;
        }

        >.burger{
            position: absolute;
            top: 3rem;
            right: 3rem;

            >svg{
                color: $main;
            }
            &:hover{
                transform: scale(1.2);
            }
        }
        >.logo{
            display: none;
        }
                .navlinks>ul {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: center;
                    gap: 1rem;
                    transform: translateX(-40%);
        
                    >li {
                        list-style-type: none;
        
                        >a {
                            padding: 10px;
                            font: 500 1rem "Open Sans";
                            color: $main;
                            transition: all 0.2s ease-in-out;
        
                            &:hover {
                                background-color: $main-light;
                                border-radius: 5px;
                            }
                        }
                    }
                }
        
                .navbuttons>ul {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: center;
                    gap: 1.5rem;
                    padding: 1rem;
                    margin: 0.8rem;
        
                    >li {
                        list-style: none;
        
                        >a {
                            padding: 6px 14px;
                            font: 500 0.95rem "Open Sans";
                            color: $main;
                            border: 1px solid $main;
                            border-radius: 5px;
                            transition: all 0.3s ease-in-out;
        
                            &:hover {
                                background-color: $main;
                                color: white;
                            }
        
                        }
        
                        .solid {
                            border: none;
